<!--留言管理组件-->
<template>
  <div class="">
    <!--功能按钮-->
    <el-button type="primary" size="small" style="margin-right: 10px" @click="selectAll">全选</el-button>
    <el-popconfirm title="确定删除吗？" @confirm="batchDelete">
      <el-button slot="reference" type="danger" icon="el-icon-delete" size="small" style="margin-bottom: 15px" >批量删除</el-button>
    </el-popconfirm>
    <el-button type="primary" size="small" style="margin-left: 10px" @click="cancel">取消选择</el-button>

    <!--数据内容-->
    <el-table
        :data="tableData"
        stripe
        border
        size="small"
        style="width: 100%">
      <el-table-column
          align="center"
          width="50">
        <template slot-scope="scope">
          <el-checkbox v-model="scope.row.checked"  @change="select(scope.row)"></el-checkbox>
        </template>
      </el-table-column>

      <el-table-column
          type="index"
          label="序号"
          align="center"
          width="50">
      </el-table-column>
      <el-table-column
          prop="imageUrl"
          label="头像"
          align="center"
          width="100">
        <template slot-scope="scope">
          <div class="demo-type">
            <el-avatar :src="scope.row.avatar"></el-avatar>
          </div>
        </template>
      </el-table-column>
      <el-table-column
          prop="nickname"
          label="昵称"
          align="center"
          width="100">
      </el-table-column>
      <el-table-column
          prop="email"
          label="邮箱"
          align="center"
         >
      </el-table-column>
      <el-table-column
          prop="messageContent"
          label="留言内容"
          align="center"
          >
      </el-table-column>
      <el-table-column
          label="创建时间"
          align="center"
          width="200">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.messageTime }}</span>
        </template>
      </el-table-column>
      <el-table-column
          label="操作"
          align="center"
          width="100">
        <template slot-scope="scope">
          <el-popconfirm title="确定删除吗？"  @confirm="deleteComment(scope.row)">
            <el-button slot="reference" type="danger" icon="el-icon-delete"   size="small">删除</el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <!--分页-->
    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page.sync="currentPage"
        :page-sizes="[6, 8, 10, 12]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
    </el-pagination>
  </div>
</template>

<script>
import request from "../../utils/request";

export default {
  name: "MessageIndex",
  components: {},
  data() {
    return {
      currentPage: 1,//当前页码
      pageSize:6,//当前每页显示条数
      total: 0,//总记录数
      //表格数据
      tableData:[
        {
          id:'',
          avatar:'https://mydwblog.oss-cn-shanghai.aliyuncs.com/2021/10/11/6350f69b77e745e2927e255b97a5e9c7.png',//用户头像
          nickname:'aa',//用户昵称
          email:'',//邮箱
          messageContent:'',//留言内容
          messageTime:'',//留言时间
          checked:'',//选中状态
        }
      ],
      //批量删除id
      ids:[],
    }
  },
//页面加载
  created() {
    this.loadMessage()
  },
//可以访问DOM元素
  mounted() {
  },
//方法集合
  methods: {
    //复选框选择方法
    select(row){
      if(row.checked){
        //被选中
        this.ids.push(row.id)
      }else {
        //取消选中
        this.removeArray(this.ids,row.id)
      }
    },
    //删除指定元素
    removeArray(arr, val) {
      for(let i = 0; i < arr.length; i++) {
        if(arr[i] === val) {
          arr.splice(i, 1);
          break;
        }
      }
    },
    //全选按钮
    selectAll(){
      if(this.ids.length==this.pageSize){
        this.$message.error("已经全选，不要重复点击")
      }
      for (let i=0;i<this.tableData.length;i++){
        this.tableData[i].checked=true;
        this.ids.push(this.tableData[i].id)
      }
    },
    //取消按钮
    cancel(){
      for (let i=0;i<this.tableData.length;i++){
        this.tableData[i].checked=false;
      }
      this.ids=[]
    },


    //批量删除方法
    batchDelete(){
      if(this.ids.length===0){
        this.$message.error("请选选择在进行批量删除")
      }else {
        for ( let i=0;i<this.ids.length;i++){
          request.delete("/message/deleteMessage/"+this.ids[i]).then(res=>{
            if(res.data.result){
              this.$message.success("删除成功")
            }else {
              this.$message.error("删除失败")
            }
          })
        }
        //刷新数据
        this.loadMessage()
        this.ids=[]
      }
    },

    //获取评论列表
    loadMessage(){
      request.get("/message/getMessagePage",{params:{
          pageNum:this.currentPage,//向后台传入的页码
          pageSize:this.pageSize,//向后台传入的当前页显示条数
        }}).then(res=>{
        //设置总记录数
        this.total=res.data.data.messagePage.total
        //设置表格数据
        this.tableData=res.data.data.messagePage.results
      })

    },
    //改变当前每一页个数
    handleSizeChange(pageSize) {
      this.pageSize=pageSize
      this.loadMessage()
    },
    //改变当前页码触发
    handleCurrentChange(pageNum) {
      this.currentPage=pageNum
      this.loadMessage()
    },

    //删除按钮
    deleteComment(row){
      request.delete("/message/deleteMessage/"+row.id).then(res=>{
        if(res.data.result){
          this.$message.success("删除成功")
          this.loadMessage()
        }else {
          this.$message.error("删除失败")
        }
      })
      this.loadMessage()
    },
  },
}
</script>

<style scoped>

</style>
        
